import { Injectable } from '@angular/core';
import{HttpClient,HttpHeaders,HttpEvent,HttpErrorResponse,HttpEventType,HttpRequest}from'@angular/common/http'; 
import { Observable } from 'rxjs';
import { Router } from '@angular/router';

@Injectable({
  providedIn: 'root'
})
export class Restapi_auditScan_Service {

  constructor(private http: HttpClient,privatehttps:HttpClient,private router: Router) { }
  url = "http://localhost:8002/wecaast/auditscan";


  startScan(auditInput,userName){
    returnthis.http.post(`${this.url}/seedUrlFunction/`+userName,auditInput).subscribe((result)=>{
   
   debugger   //ThiscodewillbeexecutedwhentheHTTPcallreturnssuccessfully
console.log(result);

if(result!=0){
  this.router.navigate(["wecaast/auditscan/reportAuditScan"]);
}

else{
  this.router.navigate(["wecaast/auditscan/seedUrlFunction"]);
}

    });
    } 


  getApplicationToFeature(applicationId:String): Observable<any> {
   
    return this.http.get(`${this.url}/getApplicationToFeature/`+applicationId);
  
  }





  pushFileToStorage(file:File):Observable<any>{
    constdata:FormData=newFormData();
    data.append('file',file);
    
    console.log("DATAAAAAAAAA:",data);
    returnthis.http.post('http://localhost:8002/wecaast/auditscan/savefile',data,{responseType:'text'as'json'}
    );
    } 




}